.title__contenedor {
  margin-top: 5rem;
  h1 {
    margin: 0;
    text-align: center;
    color: #ecf8ff;
    font-size: 3.8rem;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    margin-bottom: 0.5rem;
    @include desktop{
      font-size: 4.8rem;
      text-align: left;
    }
  }
  p {
    margin: 0;
    text-align: center;
    font-size: 1.8rem;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin-bottom: 5rem;
    @include desktop{
      font-size: 2rem;
      text-align: left;
    }
  }
}

.bar__search {
  .form {
    display: flex;
    align-items: center;
    padding: 0.5rem 1rem;
    background-color: #162129;
    input {
      width: 100%;
      background-color: transparent;
      border: none;
      outline: none;
      color: #fff;
      font-size: 2rem;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      transition: 0.3s transform ease-in-out;
    }
    button {
      cursor: pointer;
      background-color: #162129;
      border: none;
      transition: 0.3s transform ease-in-out;
      &:hover {
        transform: scale(1.2);
      }
    }
  }
}
.container__articulos--doc {
  margin-top: 5rem;
}

.articulo--grid {
  grid-template-columns: repeat(1, 1fr);
  @include telefono {
    grid-template-columns: repeat(2, 1fr);
    row-gap: 2rem;
  }
}
