.container__hero {
  margin-top: 3rem;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1.3rem;
  @include telefono {
    grid-template-columns: 1fr 0.5fr;
    align-items: center;
    height: 47rem;
    margin-top: 5rem;
  }
}

.hero__info {
  z-index: 2;
  h1 {
    text-align: center;
    font-size: 3.5rem;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    margin: 0;
    @include telefono {
      text-align: left;
      font-size: 4rem;
    }
    span {
      color: $verde;
    }
  }
  p {
    text-align: center;
    font-size: 1.4rem;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    @include telefono {
      text-align: left;
      max-width: 39.2rem;
      font-size: 1.5rem;
      font-weight: 300;
    }
  }
}
.round {
  position: relative;
  z-index: 1;
}

.hero__img {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;

  @include telefono {
    postion: absolute;
    // top: -5rem;
    right: 7rem;
    bottom: 4.5rem;
    transform: scale(1.5);
  }


  .cover {
    position: absolute;
    bottom: -0.2rem;
    width: 100%;
    height: 100%;
    background: linear-gradient(rgba(17, 26, 32, 5%), rgba(17, 26, 32, 100%));
    @include telefono {
      bottom: -0.2rem;
    }
  }

  img {
    width: calc(100% - 1rem);

  }

  .nav__social {
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-right: 1px solid $verde;
    border-bottom: 1px solid $verde;
    border-left: 1px solid $verde;
    border-radius: 0.5rem;
    height: 4.9rem;
    width: 100%;
    position: absolute;
    bottom: -3rem;
  }
}

.social__icon {
  svg {
    height: 2.4rem;
    cursor: pointer;
    transition: 0.3s all ease-in-out;
    &:hover {
      fill: $amarillo;
      transform: scale(1.2);
    }
    g {
      path {
        transition: 0.3s all ease-in-out;
        &:hover {
          fill: $amarillo;
        }
      }
    }
  }
}
