@mixin grid($colum, $gap) {
  display: grid;
  grid-template-columns: repeat($colum, 1fr);
  gap: $gap;
}

/** Media Queries **/
@mixin telefono {
  @media (min-width: 450px) {
    @content;
  }
}

@mixin tablet {
  @media (min-width:750px) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: 1040px) {
    @content;
  }
}
