html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

::-webkit-scrollbar {
  display: none;
}
body {
  animation: fade-in 1s ease-in-out;
  font-family: $font-principal;
  font-size: 1.6rem;
  line-height: 1.8;

  background-color: $principal;
}

.loaded {
  opacity: 1;
}

.contenedor {
  width: 95%;
  max-width: 76.9rem;
  margin: 0 auto;
}

p {
  color: $blanco;
  font-size: 2rem;
}
a {
  text-decoration: none;
  color: $blanco;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: $font-principal;
  margin: 5rem;
  font-weight: 300;
  text-align: center;
  color: $blanco;
}

h1 {
  font-size: 3.8rem;
}
h2 {
  font-size: 3.4rem;
}
h3 {
  font-size: 3rem;
}
h4 {
  font-size: 2.6rem;
}
