.container__skill {
  // display: flex;
  // flex-wrap: wrap;
  // gap: 1rem;
  // justify-content: center;
  display: grid;
  grid-template-columns: repeat(auto-fill,minmax(200px,1fr));
  gap: 1rem;
}

.skill {
  display: flex;
  gap: 1rem;
  align-items: center;

  min-width: 18rem;
  min-height: 6rem;
  border-radius: 0.8rem;
  border: 3px solid $color-borde;
  padding-left: 1rem;
  background: $principal;

  transition: .3s all ease-in-out;
}
.skill:hover{
  transform: scale(1.1);
  box-shadow: 0 0 .5rem $verde;
  border-color: $verde;
}


.container__skill div:last-child {
  display: flex;
  justify-content: center;
}

.skill p {
  margin: 0;
  color: $blanco;
  font-size: 2.2rem;
  font-style: normal;
  font-weight: 700;
  line-height: 180%; /* 39.6px */
}

