.footer {
  border-top: 0.2rem solid $descripcion-color;
  margin-top: 8rem;
  height: 8rem;
  display: flex;
  justify-content: space-between;
  align-items: center;

  p {
    font-size: 2rem;
    font-style: normal;
    font-weight: 600;
    line-height: 180%;
    margin: 0;
  }
  .nav__social--footer {
    .social__icon {
      margin-right: 2rem;
      &:last-child {
        margin: 0;
      }
      @include telefono {
        margin-right: 6.2rem;
      }
    }
  }
}

.link-cal{
  color: #fff;
  transition: .3s ease-in-out;
}
.link-cal:hover{
  color: $amarillo;
  transform: scale(1.2);
}