body {
  animation: fade-in 1s ease-in-out;
  font-family: "Titillium Web", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #111a20;
}

.loaded {
  opacity: 1;
}

.contenedor {
  width: 95%;
  max-width: 76.9rem;
  margin: 0 auto;
}

p {
  color: #ffffff;
  font-size: 2rem;
}

a {
  text-decoration: none;
  color: #ffffff;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: "Titillium Web", sans-serif;
  margin: 5rem;
  font-weight: 300;
  text-align: center;
  color: #ffffff;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

@keyframes vibrar {
  0%, 100% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(-5px);
  }
  50% {
    transform: translateX(5px);
  }
  75% {
    transform: translateX(-5px);
  }
}
.header__contenedor {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 5rem;
}
@media (min-width: 450px) {
  .header__contenedor {
    height: 6.3rem;
    justify-content: start;
  }
}
.header__contenedor .logo {
  font-size: 2.5rem;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}
.header__contenedor .bar {
  height: 3rem;
  display: block;
}
@media (min-width: 450px) {
  .header__contenedor .bar {
    display: none;
  }
}

.nav {
  z-index: 3;
  background-color: #111a20;
  position: absolute;
  margin-top: 5rem;
  padding: 0;
  top: 0;
  width: 100%;
  height: 100%;
  position: fixed;
  overflow-y: auto;
  left: 100%;
  transition: 0.3s left ease-in-out;
}
@media (min-width: 450px) {
  .nav {
    background-color: transparent;
    position: relative;
    margin: 0;
    top: 0;
    left: 0;
    width: auto;
    height: auto;
  }
}

.mostrar {
  left: 0;
}

.list__nav {
  list-style: none;
  width: 100%;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;
}
@media (min-width: 450px) {
  .list__nav {
    flex-direction: row;
    margin: 0;
  }
}
.list__nav .sol {
  height: 2.4rem;
  cursor: pointer;
  transition: 0.3s fill ease-in-out;
}
.list__nav .sol:hover {
  fill: #FAFE43;
}

.link {
  color: #fff;
  font-size: 1.5rem;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  transition: 0.3s all ease-in-out;
}
.link:hover {
  color: #FAFE43;
}

.active {
  color: #FAFE43;
}

.container__hero {
  margin-top: 3rem;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1.3rem;
}
@media (min-width: 450px) {
  .container__hero {
    grid-template-columns: 1fr 0.5fr;
    align-items: center;
    height: 47rem;
    margin-top: 5rem;
  }
}

.hero__info {
  z-index: 2;
}
.hero__info h1 {
  text-align: center;
  font-size: 3.5rem;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  margin: 0;
}
@media (min-width: 450px) {
  .hero__info h1 {
    text-align: left;
    font-size: 4rem;
  }
}
.hero__info h1 span {
  color: #82f9a1;
}
.hero__info p {
  text-align: center;
  font-size: 1.4rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
@media (min-width: 450px) {
  .hero__info p {
    text-align: left;
    max-width: 39.2rem;
    font-size: 1.5rem;
    font-weight: 300;
  }
}

.round {
  position: relative;
  z-index: 1;
}

.hero__img {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 450px) {
  .hero__img {
    postion: absolute;
    right: 7rem;
    bottom: 4.5rem;
    transform: scale(1.5);
  }
}
.hero__img .cover {
  position: absolute;
  bottom: -0.2rem;
  width: 100%;
  height: 100%;
  background: linear-gradient(rgba(17, 26, 32, 0.05), rgb(17, 26, 32));
}
@media (min-width: 450px) {
  .hero__img .cover {
    bottom: -0.2rem;
  }
}
.hero__img img {
  width: calc(100% - 1rem);
}
.hero__img .nav__social {
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-right: 1px solid #82f9a1;
  border-bottom: 1px solid #82f9a1;
  border-left: 1px solid #82f9a1;
  border-radius: 0.5rem;
  height: 4.9rem;
  width: 100%;
  position: absolute;
  bottom: -3rem;
}

.social__icon svg {
  height: 2.4rem;
  cursor: pointer;
  transition: 0.3s all ease-in-out;
}
.social__icon svg:hover {
  fill: #FAFE43;
  transform: scale(1.2);
}
.social__icon svg g path {
  transition: 0.3s all ease-in-out;
}
.social__icon svg g path:hover {
  fill: #FAFE43;
}

.container__articulos {
  margin-top: 9rem;
  position: relative;
}

.title__section {
  color: #ecf8ff;
  font-size: 2rem;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  margin-bottom: 1.8rem;
}
@media (min-width: 450px) {
  .title__section {
    text-align: left;
    margin-left: 0;
    margin-right: 0;
    font-size: 2.4rem;
  }
}

.articulos {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2.1rem;
}
@media (min-width: 450px) {
  .articulos {
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
}

.card__articulo {
  padding: 1rem;
  border-radius: 1rem;
  background: radial-gradient(100% 100% at 50% 0%, rgba(255, 255, 255, 0.2) 0%, rgba(255, 255, 255, 0.02) 100%);
  box-shadow: 0px 12px 24px 0px rgba(0, 0, 0, 0.6), 0px 3px 12px 0px rgba(0, 0, 0, 0.12), 0px 1px 2px 0px rgba(0, 0, 0, 0.04);
  backdrop-filter: blur(12px);
}
.card__articulo .title {
  margin: 0;
  text-align: left;
  font-size: 1.8rem;
  font-style: normal;
  font-weight: 600;
  line-height: 2.4rem;
  margin-bottom: 0.5rem;
}
.card__articulo .description {
  margin: 0;
  color: rgba(255, 255, 255, 0.6);
  font-size: 1.4rem;
  font-style: normal;
  font-weight: 400;
  line-height: 1.9rem;
  margin-bottom: 1.6rem;
}
.card__articulo .footer__card {
  display: flex;
  justify-content: space-between;
}
.card__articulo .footer__card p {
  color: rgba(130, 249, 161, 0.6);
  font-size: 1.1rem;
  margin: 0;
}
.card__articulo .footer__card a {
  margin: 0;
  color: #111a20;
  font-size: 1.1rem;
  font-style: normal;
  font-weight: 600;
  line-height: 1.3rem;
  background-color: #ffffff;
  padding: 0.1rem 0.5rem;
  border-radius: 0.8rem;
}

.container__all {
  margin-top: 3.8rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.2rem;
  transition: 0.3s all ease-in-out;
  cursor: pointer;
}
.container__all:hover {
  transform: scale(1.1);
}
@media (min-width: 450px) {
  .container__all {
    position: absolute;
    margin-top: 0.5rem;
    top: 0;
    right: 0;
  }
}
.container__all a {
  color: #ecf8ff;
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
.container__all svg {
  fill: #82f9a1;
  height: 1.2rem;
}

.container__proyectos {
  margin-top: 8rem;
  position: relative;
}

.proyectos {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2.1rem;
}
@media (min-width: 450px) {
  .proyectos {
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
}

.card__proyecto {
  display: grid;
  border-radius: 2rem;
  background: radial-gradient(100% 100% at 50% 0%, rgba(255, 255, 255, 0.2) 0%, rgba(255, 255, 255, 0.02) 100%);
  box-shadow: 0px 12px 24px 0px rgba(0, 0, 0, 0.6), 0px 3px 12px 0px rgba(0, 0, 0, 0.12), 0px 1px 2px 0px rgba(0, 0, 0, 0.04);
  backdrop-filter: blur(12px);
  padding: 0.5rem;
}
.card__proyecto img {
  border-radius: 1.5rem;
  width: 100%;
  height: 100%;
}
.card__proyecto .info__card {
  display: flex;
  gap: 1.5rem;
  align-items: center;
  margin-top: 1rem;
  margin-left: 1.6rem;
}
.card__proyecto .info__card svg path {
  width: 2.5rem;
  fill: rgba(255, 255, 255, 0.6);
}
.card__proyecto .info__card .info__title h3 {
  margin: 0;
  font-size: 1.5rem;
  font-style: normal;
  font-weight: 600;
  line-height: 2.4rem; /* 160% */
  text-align: left;
}
.card__proyecto .info__card .info__title p {
  margin: 0;
  color: rgba(255, 255, 255, 0.6);
  font-size: 1.2rem;
  font-style: normal;
  font-weight: 400;
  line-height: 2.4rem; /* 200% */
  text-align: left;
}

.proyecto__img {
  position: relative;
}
.proyecto__img .container__option {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  border-top-right-radius: 1.5rem;
  border-bottom-right-radius: 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 1rem;
  background: rgba(17, 26, 32, 0.7607843137);
  padding: 1rem;
}
@media (min-width: 450px) {
  .proyecto__img .container__option {
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 5rem;
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(17, 26, 32, 0.7607843137);
    border-radius: 1.5rem;
    transform: scale(0);
    transition: 0.5s all ease-in-out;
  }
}
.proyecto__img .container__option svg {
  height: 2.5rem;
}
@media (min-width: 450px) {
  .proyecto__img .container__option svg {
    height: 3rem;
    transition: 0.3s all ease-in-out;
    cursor: pointer;
  }
  .proyecto__img .container__option svg:hover {
    transform: scale(1.1);
    fill: #82f9a1;
  }
}
@media (min-width: 450px) {
  .proyecto__img:hover .container__option {
    transform: scale(1);
  }
}

.container__skill {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 1rem;
}

.skill {
  display: flex;
  gap: 1rem;
  align-items: center;
  min-width: 18rem;
  min-height: 6rem;
  border-radius: 0.8rem;
  border: 3px solid #293548;
  padding-left: 1rem;
  background: #111a20;
  transition: 0.3s all ease-in-out;
}

.skill:hover {
  transform: scale(1.1);
  box-shadow: 0 0 0.5rem #82f9a1;
  border-color: #82f9a1;
}

.container__skill div:last-child {
  display: flex;
  justify-content: center;
}

.skill p {
  margin: 0;
  color: #ffffff;
  font-size: 2.2rem;
  font-style: normal;
  font-weight: 700;
  line-height: 180%; /* 39.6px */
}

.footer {
  border-top: 0.2rem solid rgba(255, 255, 255, 0.6);
  margin-top: 8rem;
  height: 8rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer p {
  font-size: 2rem;
  font-style: normal;
  font-weight: 600;
  line-height: 180%;
  margin: 0;
}
.footer .nav__social--footer .social__icon {
  margin-right: 2rem;
}
.footer .nav__social--footer .social__icon:last-child {
  margin: 0;
}
@media (min-width: 450px) {
  .footer .nav__social--footer .social__icon {
    margin-right: 6.2rem;
  }
}

.link-cal {
  color: #fff;
  transition: 0.3s ease-in-out;
}

.link-cal:hover {
  color: #FAFE43;
  transform: scale(1.2);
}

.title__contenedor {
  margin-top: 5rem;
}
.title__contenedor h1 {
  margin: 0;
  text-align: center;
  color: #ecf8ff;
  font-size: 3.8rem;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  margin-bottom: 0.5rem;
}
@media (min-width: 1040px) {
  .title__contenedor h1 {
    font-size: 4.8rem;
    text-align: left;
  }
}
.title__contenedor p {
  margin: 0;
  text-align: center;
  font-size: 1.8rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin-bottom: 5rem;
}
@media (min-width: 1040px) {
  .title__contenedor p {
    font-size: 2rem;
    text-align: left;
  }
}

.bar__search .form {
  display: flex;
  align-items: center;
  padding: 0.5rem 1rem;
  background-color: #162129;
}
.bar__search .form input {
  width: 100%;
  background-color: transparent;
  border: none;
  outline: none;
  color: #fff;
  font-size: 2rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  transition: 0.3s transform ease-in-out;
}
.bar__search .form button {
  cursor: pointer;
  background-color: #162129;
  border: none;
  transition: 0.3s transform ease-in-out;
}
.bar__search .form button:hover {
  transform: scale(1.2);
}

.container__articulos--doc {
  margin-top: 5rem;
}

.articulo--grid {
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 450px) {
  .articulo--grid {
    grid-template-columns: repeat(2, 1fr);
    row-gap: 2rem;
  }
}

.container-calculadora {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.container-calculadora .formulario {
  padding: 2rem 2rem;
  border-radius: 20px;
  border: 1px solid #fff;
  background: rgba(0, 131, 202, 0.4);
  box-shadow: 0px 12px 24px 0px rgba(0, 0, 0, 0.6), 0px 3px 12px 0px rgba(0, 0, 0, 0.12), 0px 1px 2px 0px rgba(0, 0, 0, 0.04);
  backdrop-filter: blur(12px);
  width: 44.7rem;
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
  position: relative;
}
.container-calculadora .formulario .logo-ucss {
  position: absolute;
  width: 10rem;
  top: -4rem;
  right: -3rem;
  border-radius: 100%;
  background-color: rgba(0, 131, 202, 0.8);
