.container-calculadora {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;

  .formulario {
    padding: 2rem 2rem;
    border-radius: 20px;
    border: 1px solid #fff;
    background: rgba(0, 131, 202, 0.4);
    box-shadow: 0px 12px 24px 0px rgba(0, 0, 0, 0.6),
      0px 3px 12px 0px rgba(0, 0, 0, 0.12), 0px 1px 2px 0px rgba(0, 0, 0, 0.04);
    backdrop-filter: blur(12px);
    width: 44.7rem;

    display: flex;
    flex-direction: column;
    gap: 2.5rem;
    position: relative;

    .logo-ucss {
      position: absolute;
      width: 10rem;
      top: -4rem;
      right: -3rem;
      border-radius: 100%;
      background-color: rgba(0, 131, 202, 0.8);
      box-shadow: 0px 12px 24px 0px rgba(0, 0, 0, 0.6),
        0px 3px 12px 0px rgba(0, 0, 0, 0.12),
        0px 1px 2px 0px rgba(0, 0, 0, 0.04);
      @include desktop {
        width: 15rem;
        top: -6rem;
        right: -7rem;
      }
    }

    .container-input {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 0.8rem;

      label {
        color: #fff;
        font-size: 1.8rem;
      }
      input {
        font-size: 2rem;
        min-width: 15rem;
        text-align: center;
        color: #fff;
        padding: 1.6rem 2rem;
        border: none;
        border-radius: 8px;
        background: var(--Rellenos, #1c1e22);
        box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
        outline: none;
      }

      /* Quitar los controles de los inputs*/
      input[type="number"]::-webkit-inner-spin-button,
      input[type="number"]::-webkit-outer-spin-button {
        -webkit-appearance: none;
        margin: 0;
      }
      input[type="number"] {
        -moz-appearance: textfield;
      }
    }
    .btn {
      padding: 1.6rem 0.8rem;
      border: none;
      color: #fff;
      border-radius: 8px;
      background: rgba(0, 131, 202, 1);
      font-size: 1.8rem;
      font-style: normal;
      font-weight: 500;
      line-height: 16px;
      cursor: pointer;
    }
  }
}

.resultado {
  display: flex;
  background: rgba(0, 131, 202, 0.2);
  width: 100%;
  padding: 1rem 2rem;
  border-radius: 0.8rem;
  flex-direction: column;
  align-items: center;

  .btn-close {
    background: transparent;
    border: none;
    width: fit-content;
    height: fit-content;
    border-radius: 100%;
    position: absolute;
    top: 1rem;
    right: 1rem;
    font-size: 2.4rem;
    color: #ecf8ff;
    cursor: pointer;
  }
  p {
    margin: 0.5rem 0 0 0;
    text-align: center;
    color: #ecf8ff;
    font-size: 2.4rem;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }
  span {
    text-align: center;
    color: #ecf8ff;
    font-size: 2.8rem;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
  }
}

.mostrar {
  display: flex;
}

.desaprobado {
  background-color: #bf0426;
}
.aprobado {
  background-color: #067302;
}
