.container__proyectos {
  margin-top: 8rem;
  position: relative;
}

.proyectos {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2.1rem;

  @include telefono {
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
}

.card__proyecto {
  // display: flex;
  // flex-direction: column;
  display: grid;

  border-radius: 2rem;
  background: radial-gradient(
    100% 100% at 50% 0%,
    rgba(255, 255, 255, 0.2) 0%,
    rgba(255, 255, 255, 0.02) 100%
  );
  box-shadow: 0px 12px 24px 0px rgba(0, 0, 0, 0.6),
    0px 3px 12px 0px rgba(0, 0, 0, 0.12), 0px 1px 2px 0px rgba(0, 0, 0, 0.04);
  backdrop-filter: blur(12px);
  padding: 0.5rem;

  img {
    border-radius: 1.5rem;
    width: 100%;
    height: 100%;
  }

  .info__card {
    display: flex;
    gap: 1.5rem;
    align-items: center;
    margin-top: 1rem;
    margin-left: 1.6rem;
    svg {
      path {
        width: 2.5rem;
        fill: $descripcion-color;
      }
    }
    .info__title {
      h3 {
        margin: 0;
        // color: $blanco;
        font-size: 1.5rem;
        font-style: normal;
        font-weight: 600;
        line-height: 2.4rem; /* 160% */
        text-align: left;
      }
      p {
        margin: 0;
        color: $descripcion-color;
        font-size: 1.2rem;
        font-style: normal;
        font-weight: 400;
        line-height: 2.4rem; /* 200% */
        text-align: left;
      }
    }
  }
}

.proyecto__img {
  position: relative;
  // flex: 1;
  .container__option {
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    border-top-right-radius: 1.5rem;
    border-bottom-right-radius: 1.5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 1rem;
    background: #111a20c2;
    padding: 1rem;

    @include telefono {
      flex-direction: row;
      justify-content: center;
      align-items: center;
      gap: 5rem;
      position: absolute;
      width: 100%;
      height: 100%;
      background: #111a20c2;
      border-radius: 1.5rem;
      transform: scale(0);
      transition: 0.5s all ease-in-out;
    }
    svg {
      height: 2.5rem;
      @include telefono {
        height: 3rem;
        transition: 0.3s all ease-in-out;
        cursor: pointer;
        &:hover {
          transform: scale(1.1);
          fill: $verde;
        }
      }
    }
  }
  @include telefono {
    &:hover {
      .container__option {
        transform: scale(1);
      }
    }
  }
}

