.header__contenedor {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 5rem;
  @include telefono {
    height: 6.3rem;
    justify-content: start;
  }

  .logo {
    font-size: 2.5rem;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }
  .bar {
    height: 3rem;
    display: block;
    @include telefono {
      display: none;
    }
  }
}

.nav {
  z-index: 3;
  background-color: $principal;
  position: absolute;
  margin-top: 5rem;
  padding: 0;
  top: 0;
  width: 100%;
  height: calc(100%);
  position: fixed;

  overflow-y: auto;
  left: 100%;
  transition: 0.3s left ease-in-out;

  @include telefono {
    background-color: transparent;
    position: relative;
    margin: 0;
    top: 0;
    left: 0;
    width: auto;
    height: auto;
  }
}
.mostrar {
  left: 0;
}

.list__nav {
  list-style: none;
  width: 100%;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;
  @include telefono {
    flex-direction: row;
    margin: 0;
  }

  .sol {
    height: 2.4rem;
    cursor: pointer;
    transition: 0.3s fill ease-in-out;
    &:hover {
      fill: $amarillo;
    }
  }
}

.link {
  color: #fff;
  font-size: 1.5rem;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  transition: 0.3s all ease-in-out;
  &:hover {
    color: $amarillo;
    
  }
}

.active {
  color: $amarillo;
}
