.container__articulos {
  margin-top: 9rem;
  position: relative;
}
.title__section {
  color: $color-h2;
  font-size: 2rem;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  margin-bottom: 1.8rem;

  @include telefono{
    text-align: left;
    margin-left: 0;
    margin-right: 0;
    font-size: 2.4rem;
  }
}

.articulos {
  display: grid;
  grid-template-columns: repeat(1,1fr);
  gap: 2.1rem;
  @include telefono{
    grid-template-columns: repeat(2,1fr);
    gap: 1rem;
  }
}

.card__articulo {
  padding: 1rem;
  border-radius: 1rem;
  background: radial-gradient(
    100% 100% at 50% 0%,
    rgba(255, 255, 255, 0.2) 0%,
    rgba(255, 255, 255, 0.02) 100%
  );
  box-shadow: 0px 12px 24px 0px rgba(0, 0, 0, 0.6),
    0px 3px 12px 0px rgba(0, 0, 0, 0.12), 0px 1px 2px 0px rgba(0, 0, 0, 0.04);
  backdrop-filter: blur(12px);
  .title {
    margin: 0;
    text-align: left;
    font-size: 1.8rem;
    font-style: normal;
    font-weight: 600;
    line-height: 2.4rem;
    margin-bottom: 0.5rem;
  }
  .description {
    margin: 0;
    color: $descripcion-color;
    font-size: 1.4rem;
    font-style: normal;
    font-weight: 400;
    line-height: 1.9rem;
    margin-bottom: 1.6rem;
  }
  .footer__card {
    display: flex;
    justify-content: space-between;
    p {
      color: $verde-o;
      font-size: 1.1rem;
      margin: 0;
    }
    a {
      margin: 0;
      color: $principal;
      font-size: 1.1rem;
      font-style: normal;
      font-weight: 600;
      line-height: 1.3rem;

      background-color: $blanco;
      padding: 0.1rem 0.5rem;
      border-radius: 0.8rem;
    }
  }
}

.container__all {
  margin-top: 3.8rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.2rem;
  transition: .3s all ease-in-out;
  cursor: pointer;
  &:hover{
    transform: scale(1.1);
  }
  @include telefono{
    position: absolute;
    margin-top: .5rem;
    top: 0;
    right: 0;
  }
  a {
    color: $color-h2;
    font-size: 1.6rem;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }
  svg {
    fill: $verde;
    height: 1.2rem;
  }
}
